body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.text-color a {
  color: white !important;
  margin-left: 60px;
}
.copyright {
  font-size: 14px !important;
}

.footer-text a {
  color: white;
  font-size: 14px;
  background-color: "#1f6f8b";
  margin: 10px;
}
.footer-text {
  text-align: end;
}
#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.footer-container {
  min-height: 31.7vh; /* will cover the 100% of viewport */
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 100px;
}
.footer-text a:hover {
  color: orange !important;
  font-size: 14px;
  text-decoration: none;
}

.about {
  background-color: #e7e6e1;
}

#header a {
  font-size: 14px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  color: black;
}
#header {
  height: 100px;
}
#header a:hover {
  color: orange;
}

.jumbotron {
  /*background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRKjGjdqA0KJSaRCoC8ImZvF5l05gTN4bu6hA&usqp=CAU");*/
  background-size: cover;
  color: white;
  font-size: large;
  font-weight: bold;
}
#video-background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: 1;
  width: 100%;
}

.jumbotron {
  position: relative;
  overflow: hidden;
}

.jumbotron .container {
  position: relative;
  z-index: 1;
}
#carouselCap {
  color: black;
  margin: auto;
  font-size: 40px;
  font-weight: bold;
  padding: 170px;
  text-align: left;
}
.videosect {
  height: 400px;
}

.wrapper {
  text-align: center;
}
* {
  max-width: 100%;
}

.topBar {
  height: 20px;
}

#footer .row {
  margin: auto;
  width: 1250px;
}
img {
  max-width: 100%;
  max-height: 100%;
}

.about .row {
  margin-left: 0px;
  margin-right: 0px;
}

.about .col-md-6 {
  padding-left: 0px;
  padding-right: 0px;
}
.dropdown .btn-primary {
  background-color: white;
  color: black;
  border-color: white;
  border-bottom-color: gray;
  width: 100%;
}
.dropdown .btn-primary:hover {
  background-color: white;
  color: black;
  border-bottom-color: darkgrey;
  width: 100%;
}
.dropdown .btn-primary:current {
  background-color: white;
  color: black;
  border-bottom-color: darkgrey;
}
.required:after {
  content: " *";
  color: red;
}

.required {
  font-weight: bold;
}

.pBold {
  font-weight: bold;
}

label.required {
  padding: 6px;
}
label {
  padding: 6px;
}
#icon.MuiSvgIcon-root {
  fill: black;
  cursor: pointer;
}

canvas.sigCanvas {
  background-color: rgb(233, 224, 224);
  margin: 20px 20px 20px 70px;
}

.MuiCardMedia-root {
  background-size: auto;
  padding-top: 60%;
}

@media only screen and (max-width: 768px) {
  #responsive {
    width: 100%;
  }
  #responsive-navbar-nav {
    background: rgb(31, 111, 139) !important;
    z-index: 5;
  }
  #details {
    margin: -10px;
  }
  #pBar {
    margin-top: 50px;
  }
  #sigBtn {
    margin: 10px;
  }
  canvas.sigCanvas {
    margin: 0px;
  }
  #btns {
    margin: 5px;
  }
  #checkStmts {
    display: flex;
    align-items: baseline;
    flex-flow: nowrap;
  }
}
img.card-img-top {
  background-size: auto;
  height: 20%;
  width: 50%;
  margin: auto;
  margin-top: 30px;
}

.popup-content {
  margin: auto;
  background: wheat;
  width: 50%;
  padding: 5px;
}
#btns {
  margin: 5px;
}
#btn1 {
  visibility: hidden;
}
input[type="date"]::before {
  color: #999999;
  content: attr(placeholder);
}
input[type="date"] {
  color: #ffffff;
}
input[type="date"]:focus,
input[type="date"]:valid {
  color: #666666;
}
input[type="date"]:focus::before,
input[type="date"]:valid::before {
  content: "" !important;
}
input[type="checkbox"] {
  vertical-align: middle;
  margin-right: 8px;
}
.container6 {
  height: 17em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container6 h2 {
  margin: 0;
}
#root > main > div > div > div > form > button:nth-child(14) {
  background-color: green;
  border-color: green;
}
#uncontrolled-tab-example-tabpane-home > p:nth-child(1){
  visibility: hidden;
}

